import {AppState} from '../../../../store/app.reducer';
import {RTripModel} from '../rtrip.model';
import {RTripsActions} from './rtrips.actions';

export interface RTripState extends AppState {
  RTrips: State;
}

export interface State {
  RTrips: RTripModel[];
  activeRTrip: RTripModel;
}

const initialState: State = {
  activeRTrip: null,
  RTrips: []
};

export function RTripsReducer(state = initialState, action: RTripsActions) {
  switch (action.type) {
    case 'SET_ACTIVE_RTRIP': {
      return {
        ...state,
        activeRTrip: action.payload
      };
    }
    case 'SET_RTRIPS': {
      return {
        ...state,
        RTrips: action.payload
      };
    }
    default: {
      return {
        ...state
      };
    }
  }
}
