import {Component, ElementRef, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {FormArray, FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {RTripModel} from '../rtrip.model';
import {NgbCalendar, NgbDate, NgbDateParserFormatter} from '@ng-bootstrap/ng-bootstrap';
import {ActivatedRoute, ParamMap, Router} from '@angular/router';
import {Store} from '@ngrx/store';
import {RTripState} from '../store/rtrips.reducer';
import {ToastService} from '../../../../shared/toast/toast.service';
import * as RTripActions from '../store/rtrips.actions';


interface RTripDays {
  startPoint;
  endPoint;
  attractions;
  accommodation;
  RTripDayImage;
  carry;
  distance;
  notes;
}

@Component({
  selector: 'app-saveroadtrip',
  templateUrl: './saveroadtrip.component.html',
  styleUrls: ['./saveroadtrip.component.css']
})
export class SaveroadtripComponent implements OnInit, OnDestroy {
  @ViewChild('t', {static: false}) tabSet;
  @ViewChild('meetupPoint', {static: false}) meetupPoint: ElementRef;
  @ViewChild('startPoint', {static: false}) startPoint: ElementRef;
  @ViewChild('endPoint', {static: false}) endPoint: ElementRef;

  basicForm: FormGroup;
  RTripForm: FormGroup;
  settingForm: FormGroup;
  RTripDayDetails: FormArray;

  finalRTrip = new RTripModel();

  hoveredDate: NgbDate;
  fromDate: NgbDate;
  toDate: NgbDate;
  days: any;
  currentDay: number;
  RTripDaysArray: Array<any>;
  RTripDaysArrayDummy: Array<any>;

  RTripDayImg: File = null;
  RTripImg: File = null;
  RTripImgArray: Array<any> = [];
  imgPreviewUrlRTripDay: any = '../../../assets/icons/uploadPic.png';
  imgPreviewUrlRTrip: any = '../../../assets/icons/uploadPic.png';

  id: number;
  editMode = false;

  constructor(
    private router: Router,
    private fb: FormBuilder,
    private calendar: NgbCalendar,
    public formatter: NgbDateParserFormatter,
    private store: Store<RTripState>,
    private route: ActivatedRoute,
    private toastService: ToastService
  ) {
    this.currentDay = 0;
    this.days = 3;
    this.RTripDaysArrayDummy = Array(this.days).fill(0);
    this.RTripDaysArray = [];
    this.fromDate = calendar.getToday();
    this.toDate = calendar.getNext(calendar.getToday(), 'd', this.days - 1);
  }

  ngOnInit() {

    this.basicForm = this.fb.group({
      company: [''],
      RTripDates: this.fb.array([], Validators.required),
      totalDays: ['', Validators.required],
      vehicleProvided: ['', Validators.required],
      vehicleType: ['', Validators.required],
      vehicleDescription: [''],
      languages: this.fb.group({
        hindi: [false],
        english: [false]
      }),
      totalDistance: [''],
      meetupPoint: [''],
      about: [''],
    });

    this.RTripForm = this.fb.group({
      startPoint: [''],
      endPoint: [''],
      attractions: [''],
      accommodation: [''],
      RTripDayImage: [''],
      carry: [''],
      distance: [''],
      notes: [''],
    });

    this.settingForm = this.fb.group({
      todoPlan: [''],
      RTripImage: this.fb.array([]),
      provided: [''],
      carry: [''],
      pricePerPerson: ['', Validators.required],
      minPeople: [''],
      maxPeople: [''],
      RTripTitle: ['', Validators.required],
    });


    this.RTripDayDetails = this.fb.array([]);

    this.store.select('RTrips').subscribe(rTrip => {
      if (!this.editMode) {

        if (rTrip.activeRTrip) {
          this.finalRTrip = rTrip.activeRTrip;
          if (rTrip.activeRTrip.RTripDayDetails) {
            this.RTripDaysArray = rTrip.activeRTrip.RTripDayDetails;
          }
        }
      }
    });
    // for editing a RTrip
    this.route.paramMap.subscribe((params: ParamMap) => {
        if (params.get('id')) {
          this.editMode = true;

          this.id = +params.get('id');
          this.store.select('RTrips').subscribe(rTrip => {
            this.finalRTrip = rTrip.RTrips[this.id];

            if (this.finalRTrip) {
              this.basicForm.patchValue(this.finalRTrip);
              this.settingForm.patchValue(this.finalRTrip);

              this.finalRTrip.RTripDayDetails.forEach((value: RTripDays) => {
                this.RTripDayDetails.push(this.addRTripDayDetailGroup(value));
              });

              this.RTripForm.setValue(this.RTripDayDetails.value[0]);
            } else {
              this.router.navigate(['../../manageRTrip'], {relativeTo: this.route});
            }
          });
        }
      }
    );
  }

  addRTripDayDetailGroup(value: RTripDays) {
    const group = this.fb.group({
      startPoint: [value.startPoint],
      endPoint: [value.endPoint],
      attractions: [value.attractions],
      accommodation: [value.accommodation],
      RTripDayImage: [''],
      carry: [value.carry],
      distance: [value.distance],
      notes: [value.notes],
    });

    return group;

  }

  onDateSelection(date: NgbDate) {
    if (!this.fromDate && !this.toDate) {
      this.fromDate = date;
    } else if (this.fromDate && !this.toDate && date.after(this.fromDate)) {
      this.toDate = date;
    } else {
      this.toDate = null;
      this.fromDate = date;
    }

    if (this.toDate && this.fromDate) {
      this.days = Math.floor((Date.parse(this.toDate.month + '/' + this.toDate.day + '/' + this.toDate.year) - Date.parse(this.fromDate.month + '/' + this.fromDate.day + '/' + this.fromDate.year)) / 86400000);
      this.days += 1;
    }
    this.RTripDaysArrayDummy = Array(this.days).fill(0);
  }

  isHovered(date: NgbDate) {
    return this.fromDate && !this.toDate && this.hoveredDate && date.after(this.fromDate) && date.before(this.hoveredDate);
  }

  isInside(date: NgbDate) {
    return date.after(this.fromDate) && date.before(this.toDate);
  }

  isRange(date: NgbDate) {
    return date.equals(this.fromDate) || date.equals(this.toDate) || this.isInside(date) || this.isHovered(date);
  }

  validateInput(currentValue: NgbDate, input: string): NgbDate {
    const parsed = this.formatter.parse(input);
    return parsed && this.calendar.isValid(NgbDate.from(parsed)) ? NgbDate.from(parsed) : currentValue;
  }

  addRTripDate() {
    const x = {
      startDate: this.fromDate,
      toDate: this.toDate
    };
    const obj = new FormControl(x);
    (this.basicForm.controls[`RTripDates`] as FormArray).push(obj);
    this.basicForm.patchValue({totalDays: this.days});
  }

  removeRTripDate(i: number) {
    (this.basicForm.controls[`RTripDates`] as FormArray).removeAt(i);
  }

  imgPreviewRTripDay(fileInput: any) {
    this.RTripDayImg = fileInput.target.files[0] as File;
    const mimeType = this.RTripDayImg.type;
    if (mimeType.match(/image\/*/) == null) {
      return;
    }
    const reader = new FileReader();
    reader.readAsDataURL(this.RTripDayImg);
    reader.onload = () => {
      this.imgPreviewUrlRTripDay = reader.result;
    };
  }

  imgPreviewRTrip(fileInput: any) {
    const files = fileInput.target.files;
    if (files.length >= 5) {
      for (const image of files) {
        this.RTripImgArray.push(image);
        const reader = new FileReader();
        reader.onload = (e: any) => {
          (this.settingForm.get('RTripImage') as FormArray).push(
            this.fb.group({
              url: e.target.result
            })
          );
        };
        reader.readAsDataURL(image);
      }
    } else {
      this.toastService.showDanger('Please select more than 4 images!');
    }
  }

  setRTripDay(it: number) {
    this.currentDay = it;
    if (this.RTripDayDetails.value[it]) {
      this.RTripForm.setValue(this.RTripDayDetails.value[it]);
    } else {
      this.RTripForm.reset();
    }
  }

  basicFormSubmit() {
    this.basicForm.controls[`meetupPoint`].patchValue(this.meetupPoint.nativeElement.value);
    this.dispatch(this.basicForm.value);
    this.tabSet.select('RTripForm');
  }

  RTripFormSubmit() {
    this.RTripForm.controls[`startPoint`].patchValue(this.startPoint.nativeElement.value);
    this.RTripForm.controls[`endPoint`].patchValue(this.endPoint.nativeElement.value);
    if (this.RTripDayDetails && this.RTripDayDetails.value[this.currentDay]) {
      this.RTripDayDetails.insert(this.currentDay, this.addRTripDayDetailGroup(this.RTripForm.value));
    } else {
      this.RTripDayDetails.push(this.addRTripDayDetailGroup(this.RTripForm.value));
    }
    console.log(this.RTripDayDetails.value);
    this.dispatch({RTripDayDetails: this.RTripDayDetails.value});

    const data = new FormData();
    data.append('_id', this.finalRTrip._id);
    data.append('RTripDayImage', this.RTripDayImg);
    data.append('index', (this.currentDay).toString());
    this.store.dispatch(new RTripActions.TryUploadRoadTripImage(
      data
    ));

    if (this.currentDay === this.days - 1) {
      this.tabSet.select('settingForm');
      this.currentDay = 0;
    } else {
      this.currentDay += 1;
    }
  }

  settingFormSubmit() {
    this.dispatch(this.settingForm.value);
    if (this.RTripImgArray.length) {
      const data = new FormData();
      data.append('_id', this.finalRTrip._id);
      Array.from(this.RTripImgArray).forEach(image => {
        data.append('RTripImage[]', image);
      });
      this.store.dispatch(new RTripActions.TryUploadRoadTripImage(
        data
      ));
    }
    this.router.navigate(['../manageRTrip'], {relativeTo: this.route});
  }

  private dispatch(obj) {
    console.log('inside dispatch function');
    console.log(this.finalRTrip);
    console.log('after assign');
    Object.assign(this.finalRTrip, obj);
    console.log(this.finalRTrip);
    console.log('outside assign');
    this.store.dispatch(new RTripActions.TrySaveRTrip(this.finalRTrip));
  }

  ngOnDestroy(): void {
    this.finalRTrip = null;
    this.store.dispatch(new RTripActions.SetActiveRTrip(null));
  }
}
