import {Component, OnInit} from '@angular/core';
import {RTripModel} from '../rtrip.model';
import {ActivatedRoute, Router} from '@angular/router';
import {Store} from '@ngrx/store';
import {RTripState} from '../store/rtrips.reducer';
import * as RTripAction from '../store/rtrips.actions';

@Component({
  selector: 'app-manageroadtrip',
  templateUrl: './manageroadtrip.component.html',
  styleUrls: ['./manageroadtrip.component.css']
})
export class ManageroadtripComponent implements OnInit {

  RTrips: RTripModel[];
  currentRTrip: RTripModel;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private store: Store<RTripState>
  ) {
  }

  ngOnInit() {
    this.store.dispatch(new RTripAction.TryFetchRTrips());
    this.store.select('RTrips').subscribe(tripState => {
      return this.RTrips = tripState.RTrips;
    });
  }

  editRTrip(index) {
    this.router.navigate(['../editRoadTrip', index], {relativeTo: this.route});
  }

  deleteRTrip(index: number) {
    this.currentRTrip = this.RTrips[index];
    this.currentRTrip.delFlg = true;
    this.store.dispatch(new RTripAction.TrySaveRTrip(this.currentRTrip));
    this.store.dispatch(new RTripAction.TryFetchRTrips());
  }

}
