import {Action} from '@ngrx/store';
import {RTripModel} from '../rtrip.model';

export const TRY_SAVE_RTRIP = 'TRY_SAVE_RTRIP';
export const TRY_FETCH_RTRIPS = 'TRY_FETCH_RTRIPS';
export const TRY_UPLOAD_RTRIP_IMAGE = 'TRY_UPLOAD_RTRIP_IMAGE';
export const SET_RTRIPS = 'SET_RTRIPS';
export const SET_ACTIVE_RTRIP = 'SET_ACTIVE_RTRIP';

export class TryUploadRoadTripImage implements Action {
  readonly type = TRY_UPLOAD_RTRIP_IMAGE;

  constructor(public payload: any) {
  }
}

export class TryFetchRTrips implements Action {
  readonly type = TRY_FETCH_RTRIPS;
}

export class TrySaveRTrip implements Action {
  readonly type = TRY_SAVE_RTRIP;

  constructor(public payload: RTripModel) {
  }
}


export class SetRTrips implements Action {
  readonly type = SET_RTRIPS;

  constructor(public payload: RTripModel[]) {
  }
}

export class SetActiveRTrip implements Action {
  readonly type = SET_ACTIVE_RTRIP;

  constructor(public payload: RTripModel) {
  }
}

export type RTripsActions =
  SetActiveRTrip |
  TryUploadRoadTripImage |
  SetRTrips |
  TryFetchRTrips |
  TrySaveRTrip ;

