import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {RoadtripComponent} from './roadtrip.component';
import {DashboardComponent} from './dashboard/dashboard.component';
import {SaveroadtripComponent} from './saveroadtrip/saveroadtrip.component';
import {ManageroadtripComponent} from './manageroadtrip/manageroadtrip.component';

const routes: Routes = [
  {
    path: '', component: RoadtripComponent, children: [
      {path: '', redirectTo: 'dashboard'},
      {path: 'dashboard', component: DashboardComponent},
      {path: 'addRoadTrip', component: SaveroadtripComponent},
      {path: 'editRoadTrip/:id', component: SaveroadtripComponent},
      {path: 'manageRoadTrip', component: ManageroadtripComponent},
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class RoadtripRoutingModule {
}
