import * as endPoints from '../../../../shared/serverEndpoints';
import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {HttpClient, HttpResponse} from '@angular/common/http';
import * as RTripActions from './rtrips.actions';
import {map, switchMap} from 'rxjs/operators';
import {RTripModel} from '../rtrip.model';

@Injectable()
export class RTripsEffects {
  constructor(public action: Actions, public http: HttpClient) {
  }

  @Effect()
  uploadImage = this.action.pipe(
    ofType(RTripActions.TRY_UPLOAD_RTRIP_IMAGE),
    map((data: RTripActions.TryUploadRoadTripImage) => {
      return data.payload;
    }),
    switchMap((data: any) => {
      return this.http.post(endPoints.uploadRTripImage, data, {observe: 'response'});
    }), switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {
            type: RTripActions.SET_ACTIVE_RTRIP,
            payload: response.body
          }
        ];
      } else {
        alert('Some error occured !');
        return [];
      }
    })
  );

  @Effect()
  editRTrip = this.action.pipe(
    ofType(RTripActions.TRY_SAVE_RTRIP),
    map((data: RTripActions.TrySaveRTrip) => {
      return data.payload;
    }),
    switchMap((data: RTripModel) => {
      return this.http.post(endPoints.saveRTrip, data, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {
            type: RTripActions.SET_ACTIVE_RTRIP,
            payload: response.body
          }
        ];
      } else {
        alert('Some error occured !');
        return [];
      }
    })
  );


  @Effect()
  fetchRTrips = this.action.pipe(
    ofType(RTripActions.TRY_FETCH_RTRIPS),
    switchMap(() => {
      return this.http.get(endPoints.fetchRTrips, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        console.log('inside fetch trek 200');
        return [
          {
            type: RTripActions.SET_RTRIPS,
            payload: response.body
          }
        ];
      } else {
        // alert('Some error occured !');
        return [];
      }
    })
  );


}
